.c-btn {
    @include font-family;
    display: inline-block;
    box-sizing: border-box;
    padding: 9px 20px;
    text-align: center;
    outline: none;
    text-decoration: none;
    font-weight: bold;
    cursor: pointer;

    i,
    span {
        vertical-align: middle;
    }

    &--primary {
        background: $color-font-highlight;
        border: 1px solid $color-border-highlight;
        color: $color-font-white;

        &:hover {
            background: $color-font-darker;
            border: 1px solid $color-border-darker;
        }
    }

    &--secondary {
        background: transparent;
        border: 1px solid $color-border-highlight;
        color: $color-font-highlight;

        &:hover {
            background: $color-font-darker;
            border: 1px solid $color-border-darker;
            color: $color-font-white;
        }
    }

    &--disabled {
        background: $color-bg-disable;
        border: 1px solid $color-border-disable;
        color: $color-font-white;
        cursor: default;
        pointer-events: none;

        &:hover {
            background: $color-bg-disable;
            border: 1px solid $color-border-disable;
            color: $color-font-white;
        }
    }

    &--right {
        span {
            margin-right: 5px;
        }
    }

    &--left {
        span {
            margin-left: 5px;
        }
    }

    &--lg {
        padding: 15px 30px;
    }

    &--sm {
        padding: 5px 10px;
    }
}

.l-actions {
    text-align: center;
    font-size: 0;

    &>.c-btn {
        font-size: 16px;

        &+.c-btn {
            margin-left: 32px;
        }
    }
}