a {
    color: $color-font-base;
    text-decoration: none;
    outline: 0;

    &:hover {
        text-decoration: none;
    }

    &[data-tooltip],
    &[data-tooltip-emoticon] {
        cursor: pointer;
    }
}

.c-link {
    color: $color-font-base;

    &--gn {
        font-weight: bold;
        color: $color-font-highlight;

        &:hover {
            color: $color-font-highlight;
        }
    }

    &--underline {
        text-decoration: underline;
    }

    &:hover {
        color: $color-font-highlight;
    }
}

.c-iconLink {
    display: inline-block;
    text-decoration: none;
    vertical-align: middle;
    color: $color-font-main;
    position: relative;

    &:before,
    &:after {
        display: inline-block;
        vertical-align: middle;
        background-position: 0 0;
        background-repeat: no-repeat;
        background-size: contain;
    }

    &:before {
        content: "";
    }

    &:hover {
        color: $color-font-highlight;
    }

    &[class~=is-active] {
        color: $color-font-highlight;
        font-weight: bold;
    }

    &>span,
    &>i {
        display: inline-block;
        vertical-align: middle;
    }

    &--suffix {
        &:before {
            content: none;
        }

        &:after {
            content: "";
        }
    }

    &--gn {
        font-weight: bold;
        color: $color-font-highlight;

        &:hover {
            color: $color-font-highlight;
        }
    }

    &--btn {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 15px 0;
    }
}