.t1,
.t2,
.t3,
.t4,
.t5,
.t6 {
    margin-top: 0;
    margin-bottom: 0;
    font-weight: bold;
    line-height: 1.5;
    color: inherit;
    text-align: inherit;

    &--normal {
        font-weight: normal;
    }
}

.t1 {
    font-size: 30px;
}

.t2 {
    font-size: 24px;
}

.t3 {
    font-size: 20px;
}

.t4 {
    font-size: 18px;
}

.t5 {
    font-size: 16px;
}

.t6 {
    font-size: 14px;
}

.o-fBold {
    font-weight: bold;
}

.o-fNormal {
    font-weight: normal;
}