.c-icon {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;

    &:before,
    &:after {
        display: inline-block;
        width: 20px;
        height: 20px;
        background-size: 20px;
        vertical-align: middle;
        background-position: 0 0;
        background-repeat: no-repeat;
    }

    &:before {
        content: "";
    }

    &>span {
        display: inline-block;
        margin-left: 5px;
        vertical-align: middle;
        font-size: 1rem;
        color: $color-font-main;
    }

    &--suffix {
        &:before {
            content: none;
        }

        &:after {
            content: "";
        }

        &>span {
            margin-left: 0;
            margin-right: 5px;
        }
    }

    // effect
    &--effect {
        transition: all 0.3s;

        &:hover {
            transform: rotateZ(180deg);
        }
    }

    // size
    &--max {

        &:before,
        &:after {
            width: 50px;
            height: 50px;
            background-size: 50px;
        }
    }

    &--lg {

        &:before,
        &:after {
            width: 30px;
            height: 30px;
            background-size: 30px;
        }
    }

    &--md {

        &:before,
        &:after {
            width: 15px;
            height: 15px;
            background-size: 15px;
        }
    }

    &--min {

        &:before,
        &:after {
            width: 10px;
            height: 10px;
            background-size: 10px;
        }
    }

    // Wt
    &--closeWt {

        &:before,
        &:after {
            background-image: url(../images/icon/wt/close-wt.svg);
        }
    }

    &--crownWt {

        &:before,
        &:after {
            background-image: url(../images/icon/wt/crown-wt.svg);
        }
    }

    // gy
    &--homeGy {

        &:before,
        &:after {
            background-image: url(../images/icon/gy/home-gy.svg);
        }
    }

    //Hover
    &--hvrCrownOg {

        &:before,
        &:after {
            background-image: url(../images/icon/og/crown-og.svg);
        }

        a:hover & {

            &:before,
            &:after {
                background-image: url(../images/icon/wt/crown-wt.svg);
            }
        }
    }


    //Function
    &--toggle {
        &:before {
            background-image: url(../images/icon/chevron_down_gy.svg);
            transform: rotateZ(0)
        }

        .is-chosen & {
            &:before {
                transform: rotateZ(180deg)
            }
        }
    }
}