.l-breadcrumb {
    margin-bottom: 20px;
}

.c-breadCrumb {
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-size: 0;

    li {
        display: inline-block;
        vertical-align: middle;

        a {
            font-size: 14px;
            line-height: 1em;
            color: $color-font-gray;
        }

        &+li {
            position: relative;
            padding-left: 20px;

            &:before {
                content: '';
                position: absolute;
                left: 5px;
                top: 50%;
                margin-top: -5px;
                display: block;
                width: 10px;
                height: 10px;
                background-size: 10px;
                background-image: url(../images/icon/chevron_right_gy.svg);
                background-repeat: no-repeat;
                background-position: center;
            }
        }
    }
}

.c-collapse {
    border: 1px solid $color-border-highlight;
    border-radius: 4px;

    &+& {
        margin-top: 8px;
    }

    &__title {
        position: relative;
        padding: 8px 48px 8px 16px;
        background-color: $color-bg-highlight;
        color: $color-font-white;
        cursor: pointer;

        &:after {
            content: '';
            position: absolute;
            right: 16px;
            top: 50%;
            margin-top: -5px;
            display: block;
            width: 10px;
            height: 10px;
            background-size: 10px;
            background-image: url(../images/icon/chevron_down_wt.svg);
        }
    }

    &__content {
        max-height: 0;
        padding: 0 16px;
        overflow: hidden;
        transition: max-height .3s, padding .3s;
    }

    &.is-active & {
        &__title {
            font-weight: bold;

            &:after {
                background-image: url(../images/icon/chevron_up_wt.svg);
            }
        }

        &__content {
            max-height: 500px;
            padding-bottom: 16px;
        }
    }

    &--storage {
        border: 0;

        &>.c-collapse__title {
            margin-bottom: 8px;
            padding: 0 24px 0 0;
            text-align: right;
            background-color: transparent;
            color: $color-font-gray;

            &:after {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                margin-top: -8px;
                display: block;
                width: 16px;
                height: 16px;
                background-size: 16px;
                background-image: url(../images/icon/collapse-add-gy.svg);
            }
        }

        &>.c-collapse__content {
            padding-right: 0;
            padding-left: 0;
            max-height: none;
        }

        &.is-storage {
            &>.c-collapse__title {
                &:after {
                    background-image: url(../images/icon/collapse-minus-gy.svg);
                }
            }
        }
    }
}

.c-alert {
    position: relative;
    padding: 8px 60px 8px 20px;
    background-color: $color-bg-highlight;
    color: $color-font-white;

    &__close {
        position: absolute;
        right: 20px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 2;
    }
}


.block {
    box-sizing: border-box;
    padding: 50px 0;

    &.-pk {
        position: relative;
        height: 100vh;
        text-align: center;
        background-color: lightpink;

        .wrap {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &.-gn {
        background-color: darkseagreen;
    }

    &.-og {
        background-color: peachpuff;
    }

    &.-bu {
        background-color: lightskyblue;
    }

    &.-pl {
        background-color: darkslateblue;
    }
}

.wrap {
    box-sizing: border-box;
    max-width: 1024px;
    margin: auto;
    padding: 0 20px;
}

h1 {
    font-size: 60px;
    // opacity: 0;
}

h2 {
    font-size: 48px;
    // opacity: 0;
}

p {
    font-size: 20px;
    line-height: 1.5;
}