$grid: (columns: 24, gutter: 0) !default;

@mixin grid($device: null) {
    $grid-columns: map-get($grid, "columns");
    $bp: "";

    @if ($device) {
        $bp: "\\@"+ str-slice($device, 1, 1);
    }

    @for $i from 1 through $grid-columns {
        &__#{$i}#{$bp} {
            width: (100% / $grid-columns * $i);
        }
    }
}

[class^="l-cols__"] {
    flex: 0 0 auto;
    float: left;
    position: relative;
    min-height: 1px;
    padding-right: 0;
    padding-left: 0;
}

.l-cols {
    display: block;
    position: relative;
    height: auto;
    margin-right: 0;
    margin-left: 0;
    box-sizing: border-box;
    zoom: 1;


    @include clearfix;
    @include grid;

    @include breakpoint-down("mobile") {
        @include grid("mobile");
    }
}