.u-gapH {
    margin: 0 $gap;

    &--min {
        margin: 0 $gap-min;
    }

    &--sm {
        margin: 0 $gap-sm;
    }

    &--md {
        margin: 0 $gap-md;
    }

    &--lg {
        margin: 0 $gap-lg;
    }

    &--max {
        margin: 0 $gap-max;
    }
}

.u-gapV {
    margin: $gap 0;

    &--min {
        margin: $gap-min 0;
    }

    &--sm {
        margin: $gap-sm 0;
    }

    &--md {
        margin: $gap-md 0;
    }

    &--lg {
        margin: $gap-lg 0;
    }

    &--max {
        margin: $gap-max 0;
    }

}

.u-gapNextV {
    &+& {
        margin-top: $gap;
    }

    &--min+&--min {
        margin-top: $gap-min;
    }

    &--sm+&--sm {
        margin-top: $gap-sm;
    }

    &--md+&--md {
        margin-top: $gap-md;
    }

    &--lg+&--lg {
        margin-top: $gap-lg;
    }

    &--max+&--max {
        margin-top: $gap-max;
    }
}

.u-gapNextH {
    &+& {
        margin-left: $gap;
    }

    &--min+&--min {
        margin-left: $gap-min;
    }

    &--sm+&--sm {
